button {
  background-color: #1b1b1b;
  color: #fff; }

.comments-header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e6e6e6;
  padding-bottom: 15px;
  h2 {
    font-size: 18px;
    font-weight: 400; } }

.comment {
  margin: 10px 0;
  border-bottom: 1px solid #ffbf94; }

.comment-text {
  padding: 5px 0; }

.comment-toggle {
  display: block;
  margin: 10px 0;
  &.open {
    display: block;
    background-color: #fffbf2; }
  &.close {
    display: inline-block;
    background-color: #ffeabb; } }

.comment-author {
  padding-bottom: 8px;
  color: #A0A0A0;
  .name {
    text-decoration: underline; }
  .time {
    margin-left: 10px; } }

.comments-wrap > .comment .comments-wrap {
  margin-left: 30px; }

.comments-wrap > .comment:first-child {
  border-bottom: 1px solid #f7721a; }

.comments-wrap > .comment:last-child {
  border-bottom: none; }
